.hero {
  padding-bottom: 128px;
}
.hero h1 {
  font-size: 32px;
  font-weight: 700;
  margin-top: 0;
  margin-bottom: 0;
}
@media screen and (min-width: 576px) {
  .hero h1 {
    max-width: 600px;
    font-size: 64px;
  }
}
.hero p {
  font-size: 11px;
  font-weight: 400;
  margin-top: 24px;
  margin-bottom: 24px;
  color: var(--primary-black-train);
}
@media screen and (min-width: 576px) {
  .hero p {
    max-width: 600px;
    font-size: 16px;
  }
}